/* TodoTemplate */

.TodoTemplate {
  overflow: hidden;
  width: 512px;
  margin: 6rem auto;
  border-radius: 4px;
  .app-title {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 4rem;
    background: #22b8cf;
    color: #fff;
    font-size: 1.5rem;
  }
  .content {
    background: #fff;
  }
}